.fl-fb-blink-red {
  animation-name: blinkOutline;
  animation-duration: 0.3s;
  animation-iteration-count: 2;
  animation-direction: reverse; }

@keyframes blinkOutline {
  0% {
    outline: 1px solid transparent; }
  50% {
    outline: 1px solid rgba(250, 0, 0, 0.8); }
  100% {
    outline: 1px solid transparent; } }

.fl-fb {
  color: #444; }

.fl-fb-ComponentsContainer {
  display: flex;
  flex-flow: column wrap;
  align-items: stretch; }

.fl-fb-FormComponent {
  position: relative;
  padding: 0 15px 10px;
  margin: 5px 0;
  border-radius: 5px;
  transition: transform .2s ease-out, box-shadow .2s ease-out, background-color .2s ease-out; }
  .fl-fb-FormComponent:hover {
    background-color: #ebf4fc; }

.fl-fb-FormComponent-editable[contenteditable="true"] {
  border-bottom: 1px solid #444; }

.fl-fb-FormComponent-option {
  display: block; }

.fl-fb-FormComponent-option-text {
  margin-left: 5px; }

.fl-fb-FormComponent-sidebar {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  min-height: 100%;
  padding: 5px 10px;
  border-radius: 0 5px 5px 0;
  text-align: center;
  background-color: #eee;
  transition: all .1s ease, visibility 0s, box-shadow .2s;
  visibility: hidden;
  opacity: 0;
  transition: all .2s ease, visibility 0s; }
  .fl-fb-FormComponent:hover .fl-fb-FormComponent-sidebar,
  .fl-fb-FormComponent--configuration-visible .fl-fb-FormComponent-sidebar {
    visibility: visible;
    opacity: 1; }

.fl-fb-FormComponent-sidebar-btn {
  background-color: transparent;
  border: 0;
  opacity: .6; }
  .fl-fb-FormComponent-sidebar-btn:hover {
    opacity: 1; }

.fl-fb-FormComponent-configuration {
  background-color: #eee;
  width: 100%;
  padding: 15px 15px 10px;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s ease, visibility 0s;
  position: absolute;
  right: 0;
  bottom: 5px;
  z-index: 2;
  transform: translateY(100%);
  border-radius: 0 0 5px 5px; }
  .fl-fb-FormComponent--configuration-visible .fl-fb-FormComponent-configuration {
    visibility: visible;
    opacity: 1; }

.fl-fb-FormComponent-configuration-buttons,
.fl-fb-FormComponent-configuration-options {
  display: flex;
  flex-flow: row nowrap;
  align-items: stretch;
  margin-bottom: 10px; }

.fl-fb-FormComponent-configuration-btn-delete {
  margin-left: 5px; }

.fl-fb-FormComponent-configuration-btn-ok {
  margin-left: auto; }

[class*=fl-fb-FormComponent-configuration-options-btn] {
  background-color: transparent;
  color: #999;
  border: 0; }

.fl-fb-FormComponent-configuration-options-input {
  margin-left: 5px;
  min-width: 50%; }

.fl-fb-ui-switch {
  display: inline-block;
  margin: 0 5px; }
  .fl-fb-ui-switch .fl-fb-ui-switch-toggle {
    position: absolute;
    margin-left: -9999px;
    visibility: hidden; }
  .fl-fb-ui-switch .fl-fb-ui-switch-toggle + label {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    cursor: pointer;
    outline: none;
    user-select: none; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label {
    padding: 2px;
    width: calc( 20px * 2);
    height: 20px;
    background-color: #dddddd;
    border-radius: 20px; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label:after,
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label:before {
    display: block;
    position: absolute;
    top: 1px;
    left: 1px;
    bottom: 1px;
    content: ""; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label:before {
    right: 1px;
    background-color: #f1f1f1;
    border-radius: 20px;
    transition: background 0.4s; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label:after {
    width: calc( 20px - 2px);
    background-color: #fff;
    border-radius: 100%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    transition: margin 0.4s; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round:checked + label:before {
    background-color: #63A8EB; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round:checked + label:after {
    margin-left: 20px; }

.fl-fb-ControlBar {
  display: flex;
  flex-flow: row wrap;
  align-items: stretch;
  padding-bottom: 10px; }

[class*='fl-fb-ControlBar-button'] {
  margin: 3px; }
